
import Toast from 'vux/src/components/toast/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import { getValidateCodeApi } from '@/api/login'
import { getWxShareInfo } from '@/utils/wxshare'

export default {
name: 'login',
components: {
Toast,
XDialog
},
data() {
return {
showRulesBox: false,
name: '',
mobile: '',
validateCode: '',
disabled: false,
timerNum: 59
}
},
created() {

},
mounted() {
this.$nextTick(function() {
getWxShareInfo()
})
},
methods: {
getValidateCode() {
if (!this.mobile) {
this.showToast({
text: '请输入手机号',
type: 'cancel'
})
return false
}
this.disabled = true
getValidateCodeApi({
mobile: this.mobile.trim()
}).then((response) => {
if (response.code * 1 === 0) {
this.showToast({
text: '验证码已发送',
type: 'success'
})
} else {
this.showToast({
text: response.message,
type: 'cancel'
})
this.disabled = false
this.timerNum = 59
}
}).catch(() => {
})

let auth_timetimer = setInterval(() => {
this.timerNum--
if (this.timerNum <= 0) {
this.disabled = false
this.timerNum = 59
clearInterval(auth_timetimer)
}
}, 1000)
},
handleLogin() {
if (!this.name) {
this.showToast({
text: '请输入姓名',
type: 'cancel'
})
return false
}
if (!this.mobile) {
this.showToast({
text: '请输入手机号',
type: 'cancel'
})
return false
}
if (!this.validateCode) {
this.showToast({
text: '请输入验证码'
})
return false
}
this.$store.dispatch('Login', {
name: this.name,
mobile: this.mobile,
validateCode: this.validateCode
}).then((response) => {
this.$router.push({ path: '/landlord' })
}).catch(() => {
console.log(1)
})
}
}
}



